import SpinnerIcon from '@/assets/icons/spinner.svg';
export default {
  props: ['item'],
  components: {
    SpinnerIcon
  },
  computed: {
    action() {
      if (this.item.status === 'INITIATION_CONFIRMED') {
        return 'Fund';
      } else if (this.item.status === 'READY_TO_CLAIM') {
        return 'Claim';
      } else if (this.item.status === 'GET_REFUND') {
        return 'Refund';
      }
      return null;
    }
  }
};